import React, {Component} from 'react';
import {render} from 'react-dom';
import {Form, Input, InputNumber, Button, Row, Col, Select} from 'antd';
import axios from 'axios';
import Top from './../../component/top';
// import Footer from "../../component/footer";
import './index.css';

const { Option } = Select;
const layout = {
    labelCol: {span: 8},
    wrapperCol: {span: 16},
};

const validateMessages = {
    required: '!',
    types: {
        email: 'Not a validate email!',
        number: 'Not a validate number!',
    },
    number: {
        range: 'Must be between ${min} and ${max}',
    },
};


class ForgotYour extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
    }
    onFinish = (values) => {
        //
        axios.post('/company/register',{
            ...values
        })
            .then(function(response) {
                console.log(response.data);
                console.log(response.status);
                console.log(response.statusText);
                console.log(response.headers);
                console.log(response.config);
            });

    };
    onReset = () => {
        this.props.form.resetFields();
    };
    render() {
        return (
            <div className="login-template">
                <Top />
                <div className="login-form-box">
                    <div className="login-title"></div>
                    <Form {...layout} name="nest-messages" onFinish={this.onFinish} validateMessages={validateMessages}>
                        <Form.Item name="username" label="" rules={[{required: true}]}>
                            <Input placeholder=""/>
                        </Form.Item>
                        <Form.Item label="">
                            <Form.Item name="yzm" noStyle  rules={[{required: true}]}>
                                <Input placeholder="" min={1} max={10} style={{width:200,marginRight:10}} />
                            </Form.Item>
                            <Button className="ant-form-text"> </Button>
                        </Form.Item>
                        <Form.Item name='password' label="" rules={[{required: true}]}>
                            <Input.Password placeholder="6-25"/>
                        </Form.Item>
                        <Form.Item
                            name={['user', 'introduction']}
                            label=""
                            dependencies={['password']}
                            hasFeedback
                            rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                                ({ getFieldValue }) => ({
                                    validator(rule, value) {
                                        if (!value || getFieldValue('password') === value) {
                                            return Promise.resolve();
                                        }

                                        return Promise.reject('!');
                                    },
                                }),
                            ]}>
                            <Input.Password placeholder=""/>
                        </Form.Item>
                        <Form.Item wrapperCol={{span: 19, offset: 5}} className="login-button">
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                            <p><a href="/login"></a></p>
                        </Form.Item>
                    </Form>
                </div>
                {/*<Footer/>*/}
            </div>
        );
    };
}

export default ForgotYour;