import React, {Component} from 'react';
import {render} from 'react-dom';
import {Form, Input, InputNumber, Button, Row, Col, Select} from 'antd';
import axios from 'axios';
import Top from './../../component/top';
// import Footer from "../../component/footer";
import './index.css';

const { Option } = Select;
const layout = {
    labelCol: {span: 8},
    wrapperCol: {span: 10},
};

const validateMessages = {
    required: '!',
    types: {
        email: 'Not a validate email!',
        number: 'Not a validate number!',
    },
    number: {
        range: 'Must be between ${min} and ${max}',
    },
};


class Register extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
    }
    onFinish = (values) => {
        //
        axios.post('/company/register',{
            ...values
        })
            .then(function(response) {
            console.log(response.data);
            console.log(response.status);
            console.log(response.statusText);
            console.log(response.headers);
            console.log(response.config);
        });

    };
    onReset = () => {
        this.props.form.resetFields();
    };
    render() {
        return (
            <div className="register-template">
                <Top />
                <div className="register-form-box">
                    <div className="register-title"></div>
                <Form {...layout} name="nest-messages" onFinish={this.onFinish} validateMessages={validateMessages}>
                    <Form.Item name="username" label="" rules={[{required: true}]}>
                        <Input placeholder="6-16"/>
                    </Form.Item>
                    <Form.Item name="company_name" label="" rules={[{required: true}]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item name="mobile" label="" rules={[{required: true}]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item name="mobile" label="" rules={[{required: true}]}>
                        <Select defaultValue="lucy">
                            <Option value="jack">1</Option>
                            <Option value="lucy">2</Option>
                            <Option value="Yiminghe">3</Option>
                        </Select>
                    </Form.Item>
                    <Form.Item name="mobile" label="" rules={[{required: true}]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item name='password' label="" rules={[{required: true}]}>
                        <Input.Password placeholder="6-25"/>
                    </Form.Item>
                    <Form.Item
                        name={['user', 'introduction']}
                        label=""
                        dependencies={['password']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                            ({ getFieldValue }) => ({
                                validator(rule, value) {
                                    if (!value || getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }

                                    return Promise.reject('!');
                                },
                            }),
                        ]}>
                        <Input.Password placeholder=""/>
                    </Form.Item>
                    <Form.Item label="">
                        <Form.Item name="yzm" noStyle  rules={[{required: true}]}>
                            <Input min={1} max={10} style={{width:100,marginRight:10}} />
                        </Form.Item>
                        <Button className="ant-form-text"> </Button>
                    </Form.Item>

                    <Form.Item wrapperCol={{...layout.wrapperCol, offset: 8}}>
                        <Button type="primary" htmlType="submit">
                            
                        </Button>
                        <Button htmlType="submit" className="ml15" onClick={this.onReset}>
                            
                        </Button>
                    </Form.Item>
                </Form>
                </div>
            {/*<Footer/>*/}
            </div>
        );
    };
}

export default Register;